.header .gnb .depth1 {
	display: flex;
	justify-content: space-between;
}

.header .gnb li {
	padding: 2.5rem 3rem;
}

.visual {
	background-color: #003831;
	border-radius: 0 0 1rem 1rem;
	height: 27.5rem;
	width: auto;
	position: relative;
}

.visual h2 {
	display: none;
}

.visual .swiper_visual {
	width: 100%;
	height: 100%;
}

.visual .swiper_visual .swiper-wrapper {
	width: 100%;
	height: 100%;
}

.visual .swiper_visual .swiper-slide img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: scale-down;
	border-radius: 1rem;
}

.visual .visual_pagination {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 2rem;
	right: 2rem;
}

.visual .swiper_visual .kams_tit {
	position: absolute;
	font-family: "timeline-210", sans-serif;
	bottom: 3rem;
	left: 4rem;
	z-index: 1;
	color: #fbfbfb;
	display: block;
	font-size: 4rem;
}

.kams_notice {
	margin-top: 7rem;
}

.kams_notice .notice_title_wrap {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
}

.kams_notice .notice_title_wrap .select_notice {
	display: flex;
	height: 100%;
	justify-content: space-between;
	font-size: 1.7rem;
	border-radius: 10rem;
	background-color: #f2f2f2;
	font-weight: 700;
	padding: 0 1.5rem;
}

.kams_notice .notice_title_wrap ul.select_notice>li>button {
	padding: 1.5rem;
}

.kams_notice .notice_wrap {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	border: 0.1rem solid #888888;
	margin-top: 5rem;
}

.kams_notice .notice_wrap #notice_box {
	padding: 3rem;
}

.kams_notice .notice_wrap #notice_box:nth-child(1),
.kams_notice .notice_wrap #notice_box:nth-child(2),
.kams_notice .notice_wrap #notice_box:nth-child(3) {
	border-bottom: 0.1rem solid #999999;
}

.kams_notice .notice_wrap #notice_box:nth-child(1),
.kams_notice .notice_wrap #notice_box:nth-child(2),
.kams_notice .notice_wrap #notice_box:nth-child(4),
.kams_notice .notice_wrap #notice_box:nth-child(5) {
	border-right: 0.1rem solid #999999;
}

.kams_notice .label_reg_on,
.kams_notice .label_reg_done {
	font-size: 1.8rem;
	padding: 0.6rem 1.5rem;
	border-radius: 1.8rem;
	font-weight: 700;
