.footer {
	box-shadow: 0 -0.1rem 1rem rgba(0, 0, 0, 0.5);
	background-color: #003831;
	color: #fbfbfb;
	font-size: 1.6rem;
	padding: 5rem 0 7rem 0;
}

.footer .footer_wrap {
	display: flex;
	justify-content: space-between;
}

.footer .site_info .footer_info {
	display: flex;
	justify-content: space-between;
	margin-bottom: 7rem;
}

.footer .site_info .footer_info li {
	margin-right: 3rem;
}

.footer .site_info .footer_info .privacy {
	font-weight: 800;
}

.footer .site_info .tel_no {
	display: block;
	margin: 1.5rem 0;
}

.footer .site_info .copyright br {
	display: none;
}

.footer .footer_logo {
	width: 30%;
}

